export default {
  name: "IndexPage",
  computed: {
    prevList() {
      return this.list.slice(1, this.list.length)
    }
  },
  async asyncData({ $axios }) {
    let result = await $axios.$get("web/article/list");
    if (!result) return { list: [] };
    return { list: result.data.list };
  },
};
